<template>
  <div>
    <small
      :class="'site-schedule-list-item-status-' + value"
      class="site-schedule-list-item-status d-inline-block"
    >
      {{ getStatusText(value) }}
    </small>
  </div>
</template>

<script>
export default {
  name: 'AppStatus',
  props: {
    value: { required: true }
  },
  methods: {
    getStatusText(text) {
      switch (text) {
        case 'pending':
          return 'Aguardando aprovação do barbeiro';  
          
        case 'approved':
          return 'Agendado';
          
        case 'canceled':
          return 'Cancelado';

        case 'refused':
          return 'Recusado pelo barbeiro';

        case 'finished':
          return 'Finalizado';
      
        default:
          return 'Status indefinido'
      }
    },
  }
}
</script>

<style lang="scss">
@import 'src/scss/theme.scss';

.site-schedule-list-item-status {
    color: $primary;
    font-weight: 500;

    &.site-schedule-list-item-status-approved,
    &.site-schedule-list-item-status-finished {
      color: $success !important;

      &:before { background-color: $success; }
    }

    &.site-schedule-list-item-status-canceled,
    &.site-schedule-list-item-status-refused {
      color: $danger !important;

      &:before { background-color: $danger; }
    }
    
    &:before {
      content: '';
      width: 8px;
      height: 8px;
      background-color: $primary;
      display: inline-block;
      margin-right: 5px;
      border-radius: 50%;
    }
  }
</style>