<template>
  <div v-if="schedule" class="schedule-description">
    <v-row class="topo mt-1 primary--text">
      <v-col>
        <span class="primary--text muted d-inline-block py-1 px-4 date">
          {{ formatDateTimeToStringPtBr(new Date(schedule.start_date)) }}
        </span>
      </v-col>

      <v-spacer></v-spacer>
      
      <v-col class="text-right" cols="12" sm="auto">
        <app-status :value="schedule.status" />
      </v-col>
     
      <v-col cols="12" class="py-0">
        <span class="codigo">
          Agendamento. {{ schedule.id }} 
        </span>
      </v-col>
    </v-row>

    <div v-if="schedule.cancelation_message" class="mt-8">
      <v-alert class="primary" dark>
        <strong>Motivo do cancelamento:</strong>
        <br>
        <small class="mb-0">Cancelado porque estou doente.</small>
      </v-alert>
    </div>

    <v-row class="mt-8 primary--text">
      <v-col cols="12" class="py-0">
        <h2 class="primary-dark--text">
          {{ schedule.barbershop.name }}
        </h2>
      </v-col>
      <v-col cols="12" class="py-0">
        <v-row align="center">
          <v-col v-if="schedule.barber.profile_picture" cols="auto">
            <img :src="schedule.barber.profile_picture" class="barber-name" />
          </v-col>
          <v-col class="text-left">
            <span>{{ schedule.barber.name}}</span>
            <p v-if="schedule.barber.phone">
              {{ setPhoneMask(schedule.barber.phone) }}
            </p>
          </v-col>
        </v-row>
      </v-col>
    </v-row>

    <div class="mt-10 primary--text">
      <h2>Detalhes</h2>
      <div class="mt-2">
        <h3>Serviços</h3>
  
        <ul>
          <li 
            :key="index"
            v-for="(service, index) in schedule.services"
          >
            {{ service.name }}
            <small class="ml-4 service-price">
              R$ {{ setMoneyMask(schedule.scheduling_services[index].price.toFixed(2)) }}
            </small>
          </li>
        </ul>

        <p v-if="schedule.observation" class="mt-4">
          <strong>Obs:</strong> {{ schedule.observation }}
        </p>

        <p class="mt-8">
          <strong>Total:</strong>
          <span>
            R$ {{ setMoneyMask(schedule.total.toFixed(2)) }}
          </span>
        </p>
      </div>
    </div>
    
    <div v-if="showBtn" class="text-center mt-8 mb-4">
      <btn
        text="Cancelar agendamento"
        class="danger btn-cancel"
        :block="false"
        @clicked="cancel"
      />
    </div>
  </div>
</template>

<script>
import Axios from '@/plugins/axios'
import Btn from '@/components/globais/buttons/secondary/BtnSecondary'
import DateMixin from '@/mixins/DateMixin'
import ScheduleMixin from '@/mixins/ScheduleMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import TextMixin from '@/mixins/TextMixin'
import UserMixin from '@/mixins/UserMixin'

import AppStatus from './AppStatus'

export default {
  name: 'Description',
  components: { Btn, AppStatus },
  mixins: [ DateMixin, ScheduleMixin, SnackbarMixin, TextMixin, UserMixin ],
  props: {
    scheduleId: { default: null }
  },
  data: () => ({
    schedule: null
  }),
  watch: {
    scheduleId (id) {
      this.getScheduleById(id)
    }
  },
  computed: {
    showBtn () {
      return this.schedule.status === 'pending'
    },
  },
  mounted () {
    this.getScheduleById(this.scheduleId)
  },
  methods: {
    cancel () {
      Axios.put(`schedule/${this.schedule.id}/user/cancel`,
        { user_id: this.getUser().id }
      ).then(response => {
        if (response.data.codigo === 200) this.onCancelSucess(response.data.corpo)
        else this.onCancelError(response.data.corpo)
      }).catch ((err) => {
        this.onCancelError('Não foi possível cancelar o agendamento!')
        window.console.log(err)
      })
    },
    onGetScheduleByIdSuccess (response) {
      this.schedule = response
    },
    onGetScheduleByIdError (error) {
      this.showSnackbarErro(error)
    },
    onCancelError (error) {
      this.showSnackbarErro(error)
    },
    onCancelSucess (message) {
      this.schedule.status = {
        id: this.$SCHEDULE_STATUS.CANCELADO,
        name: this.schedule.status.name = 'Cancelado'
      }
      this.showSnackbar(message)
    }
  }
}
</script>

