.date { border-radius: 5px; }
.barber-name { width: 50px; }
h2 { font-size: 1.5rem; }
h3 { font-size: 1rem; }
.service-price { font-weight: bold; }
.btn-cancel { color: #fff !important; }

.schedule-description {
  color: #fff !important;
  max-height: calc(100vh - 140px);
  overflow: hidden;
  overflow-y: auto;

  .codigo { font-size: .8rem; }
  
  ul { margin: 5px 0 0; }
  p { margin: 5px 0 0; white-space: pre; }
}
</style>